} */ }

/*----Убираем точки li----------------------------------------------------*/
.nav ul {
  list-style: none; }

/*----Добавляем перегородку перед repair----------------------------------------------------*/
/*----Flex для правой колонки (column)----------------------------------------------------*/
.nav__actions {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  min-width: 280px;
  margin-right: 15px; }

/* перенос из html в bg */
.nav__actions li:nth-of-type(1) button {
  background-image: url(./static/call.svg);
  background-repeat: no-repeat;
  width: 40px;
  height: 40px;
  margin-left: 5px; }

.nav__actions li:nth-of-type(2) button {
  background-image: url(./static/chat.svg);
  background-repeat: no-repeat;
  width: 40px;
  height: 40px;
  margin-left: 5px; }

.nav__actions li:nth-of-type(3) button {
  background-image: url(./static/profile.svg);
  background-repeat: no-repeat;
  width: 40px;
  height: 40px;
  margin-left: 5px; }

.nav__actions li:nth-of-type(4) button {
  background-image: url(./static/repair.svg);
  background-repeat: no-repeat;
  width: 40px;
  height: 40px;
  margin-left: 20px; }

.nav__actions li:nth-of-type(5) button {
  background-image: url(./static/checkstatus.svg);
  background-repeat: no-repeat;
  width: 40px;
  height: 40px;
  margin-left: 5px; }

/*----Убираем видимость кнопок transparent - прозрачный фон----------------------------------------------------*/
.nav__button {
  padding: 0;
  border: none;
  cursor: pointer;
  background-color: transparent; }

@media all and (max-width: 767px) {
  .nav__actions li:nth-child(1),
