  .nav__actions {
    min-width: 90px;
    padding-left: 0; }
  .nav__burger img {
    margin-left: 15px; } }

@media all and (min-width: 1120px) {
  .nav {
    display: none; } }

/*----Меню на экране больше 1120px----------------------------------------------------*/
.services {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 55px;
  margin-top: 24px; }

.services__headline h1 {
  font-size: 28px;
  font-weight: bold;
  font-style: normal;
  margin-bottom: 10px;
  color: #1B1C21;
  margin-bottom: 18px; }

.services__headline h1::before {
  content: "";
  display: inline-block;
  vertical-align: middle;
  border-right: 4px solid #41f6d7;
  margin-right: 20px;
  border-top-right-radius: 50%;
  border-bottom-right-radius: 50%;
  height: 45px; }

.services__actions {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  min-width: 300px; }

.services__actions div:nth-of-type(1) button {
  background-image: url(./static/repair.svg);
  background-repeat: no-repeat;
  width: 40px;
  height: 40px;
  margin-left: 5px; }

.services__actions div:nth-of-type(2) button {
  background-image: url(./static/checkstatus.svg);
  background-repeat: no-repeat;
  width: 40px;
  height: 40px;
  margin-left: 5px; }

/*----Убираем видимость меню для экрана меньше 1120px----------------------------------------------------*/
@media all and (max-width: 1120px) {
  .services__actions {
    display: none; } }

/*------*/
.services__actions_inline {
  display: inline-block;
  vertical-align: middle; }

.services__actions_inline > span {
  display: inline-block;
  vertical-align: middle;
  margin-right: 8px; }

.services__actions_inline > img {
  display: inline-block;
  vertical-align: middle;
  margin-right: 25px; }

.services__actions_inline > button {
  display: inline-block;
  vertical-align: middle;
  padding: 0;
  border: none;
  background-color: transparent;
  cursor: pointer;
  margin-right: 25px; }

.menu-scroll ul {
  list-style: none;
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: nowrap;
  overflow-x: scroll;
  overflow-y: hidden;
  scrollbar-width: none;
  height: 62px;
  padding-left: 10px; }

.menu-scroll ul::-webkit-scrollbar {
  width: 0; }

.menu-scroll ul li {
  height: 31px;
  flex-shrink: 0;
  margin: 11px 4px 11px;
  padding: 7px 10px 0; }

.menu-scroll {
  margin-left: 12px; }

/* .menu-scroll ul li:nth-child(1) {
border: 2px solid #41f6d7;
border-radius: 4px;
background: white; 
} */
@media all and (max-width: 1131px) {
  .menu-scroll ul {
    flex-wrap: nowrap;
    overflow-x: auto;
    scrollbar-width: none; }
  .menu-scroll ul::-webkit-scrollbar {
    display: none; } }

.menu-scroll ul li:hover {
  outline: 2px solid #41f6d7;
  border-radius: 4px;
  filter: opacity(0.6);
  transition-property: filter;
  transition-duration: 0.25s;
  transition-timing-function: linear; }

.menu-scroll a {
  text-decoration: none;
  color: #1B1C21;
  display: inline-block;
  vertical-align: middle; }

/*----Оформляем основной раздел---------------------------------------------------------------------*/
.main {
  display: flex; }

@media all and (max-width: 767px) {
